import { AvailableHours } from './../../models/available-hours';
import { AlertService } from 'src/app/services/alert.service';
import { CalendarService } from './../../services/calendar.service';
import { FormGroup, FormControl } from '@angular/forms';
import { EnvService, DaysOfWeek } from "src/app/services/env.service";
import { concatMap } from "rxjs/operators";
import { from } from "rxjs";
import { ModalController, PickerController } from "@ionic/angular";
import { Component, OnInit, Input } from "@angular/core";

@Component({
  selector: "app-pvd-add-available-hours",
  templateUrl: "./pvd-add-available-hours.component.html",
  styleUrls: ["./pvd-add-available-hours.component.scss"],
})
export class PvdAddAvailableHoursComponent implements OnInit {
  
  @Input() dayOfWeek;
  isSubmitWaiting = false;
  availableHoursForm = new FormGroup(
		{
			timeFrom: new FormControl("08:00"),
			timeTo: new FormControl({value:"09:00", disabled:true}),
      repeat: new FormControl({value:true, disabled:true})
		}
	);
  constructor(
    private modal: ModalController,
    private picker: PickerController,
    public env: EnvService,
    private calendarService:CalendarService,
    private alert:AlertService
  ) {
    this.availableHoursForm.controls.timeFrom.valueChanges.subscribe(result=>{
      let hours = new AvailableHours();
      hours.time_from = result;
      AvailableHours.SetTimeToBasedOnTimeFrom(hours);
      this.availableHoursForm.controls.timeTo.setValue(hours.time_to);
    })
  }
  dismiss(refresh = false) {
    this.modal.dismiss({ refresh: refresh });
  }
  selectDayOfWeek() {
    this.openDayOfWeekPicker().subscribe();
  }

  openDayOfWeekPicker() {
    return from(
      this.picker.create({
        columns: this.getColumns(),
        buttons: [
          {
            text: "انصراف",
            role: "cancel",
          },
          {
            text: "تأیید",
            handler: (value) => {
              this.dayOfWeek = value['col-1']["value"];
            },
          }
        ],
        mode: "ios",
      })
    ).pipe(
      concatMap((picker) => {
        return from(picker.present());
      })
    );
  }
  getColumns() {
    let columns = [];
    let options = [];
    for (let i = 0; i < 7; i++) {
      
      options.push({
        text: this.env.dayOfWeekText(i),
        value: i,
      });
    }
    
    columns.push({
      name: "col-1",
      options: options,
      selectedIndex:this.dayOfWeek
    });

    return columns;
  }
  submit(){
    this.isSubmitWaiting = true;
    let timeFrom = this.availableHoursForm.controls.timeFrom.value;
    let timeTo = this.availableHoursForm.controls.timeTo.value;
    let repeat = this.availableHoursForm.controls.repeat.value;
    let repeatDayOfWeek = this.dayOfWeek;
    this.calendarService.addAvailableHours(timeFrom, timeTo, repeat, repeatDayOfWeek).subscribe(result=>{
      if(result)
      {
         this.alert.presentSuccess("ساعات کاری اضافه شد.");
        this.modal.dismiss();
      }
      else
      {
        this.alert.presentError("بین ساعت های انتخابی این روز هم پوشانی وجود دارد.");
      }
       
        this.isSubmitWaiting = false;
      })
  }
  ngOnInit() {
  }
  getDayText()
  {
    return this.env.dayOfWeekText(parseInt(this.dayOfWeek));
  }
}
