import { AvailableHours } from "./../../models/available-hours";
import { CalendarService } from "./../../services/calendar.service";
import { Subscription } from "rxjs";
import { concatMap } from "rxjs/operators";
import { from } from "rxjs";
import { PvdAddAvailableHoursComponent } from "./../pvd-add-available-hours/pvd-add-available-hours.component";
import { ModalController } from "@ionic/angular";
import { Component, OnInit } from "@angular/core";
import { DaysOfWeek, EnvService } from "src/app/services/env.service";

@Component({
  selector: "app-pvd-manage-available-hours",
  templateUrl: "./pvd-manage-available-hours.page.html",
  styleUrls: ["./pvd-manage-available-hours.page.scss"],
})
export class PvdManageAvailableHoursPage implements OnInit {
  calendarSubscription: Subscription;
  constructor(
    private modalController: ModalController,
    private calendarService: CalendarService,
    public env:EnvService
  ) {}
  selectedDayOfWeek: DaysOfWeek = DaysOfWeek.Saturday;
  unfilteredAvailableHoursArray: AvailableHours[] = [];
  filteredHours:AvailableHours[] = [];
  ngOnInit() {}
  ionViewWillEnter() {
    this.calendarSubscription = this.calendarService.availableHoursUpdated.subscribe(
      (result) => {
        this.unfilteredAvailableHoursArray = result;
        this.fitlerHours();
      }
    );
    this.calendarService.getAvailableHours().subscribe();
  }
  add() {
    this.presentAddAvailableHoursModal().subscribe();
  }
  ionViewWillLeave() {
    this.calendarSubscription.unsubscribe();
  }
  segmentChanged(ev)
  {
    this.selectedDayOfWeek = ev.target.value;
    this.fitlerHours();
  }
  fitlerHours() {
      this.filteredHours = this.unfilteredAvailableHoursArray.filter(
        (hour) => {
          return hour.repeating_day_of_week == this.selectedDayOfWeek;
        }
      );
    
  }
  refresh(ev)
  {
    this.calendarService.getAvailableHours().subscribe(result=>{
      ev.target.complete();
    });
  }
  delete(id)
  {
    this.calendarService.removeAvailableHours(id).subscribe();
  }
  toggleDisabledHours(id)
  {
    this.calendarService.toggleDisabledAvailableHours(id).subscribe();
  }
  presentAddAvailableHoursModal() {
    return from(
      this.modalController.create({
        component: PvdAddAvailableHoursComponent,
        componentProps:{
          dayOfWeek: this.selectedDayOfWeek
        }
      })
    ).pipe(
      concatMap((modal) => {
        modal.onDidDismiss;
        from(modal.onDidDismiss()).subscribe((data) => {});
        return from(modal.present());
      })
    );
  }
}
